<template>
<center>
<div>
<ul>
<li v-for="qnaItem in qnalist" v-bind:key="qnaItem.num">
<i class="checkBtn fab fa-quora"></i>
<span v-show="qnaItem.comment!=null" style="font-weight:bold; color:blue; font-size:5px">[답변완료] </span>
<span style="text-align:center;" @click="getQna(qnaItem.num)">{{ qnaItem.title }}</span>
<span v-show="qnaItem.wdate.substring(0,10)==(new Date().getFullYear()+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate())" style="color:red; font-size:3px">  New </span>
<span class="removeBtn" type="button" @click="removeQna(qnaItem.num)">
<i class="far fa-trash-alt"></i>
</span>
</li>
</ul>
</div>
</center>
</template>

<script>
import Constant from "../Constant";

export default {
created() {
this.$store.dispatch(Constant.GET_QNALIST); //action call
},
computed: {
//store안의 qnalist check
qnalist() {
return this.$store.state.qnalist;
}
},
methods: {
removeQna: function(num) {

this.$store.dispatch(Constant.REMOVE_QNA, { num });
},
getQna: function(num) {
this.$router.push("/detail/" + num);
}
}
};
</script>

